@import "assets/styles/abstract/index.scss";

:root {
  --nav-size: 300px;
  --border-circle: 50%;

  --main-color: #000000;
}

.navigation__content {
  display: flow-root;
}

.navigation {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: var(--nav-size);
  margin-left: calc(-1 * var(--nav-size));
  z-index: 10;
  transition: margin-left 0.5s ease;
  background: $color-title-text;
}
.navigation.isOpen{
  margin-left: 0;
}

.avatar_icon {
  height: $avatar-size;
  width: $avatar-size;
  padding: $avatar-picture-padding 0;
  margin: auto;

  .close-icon {
    color: $color-button-text;
  }  

  @include vertical-align;
}

.close-icon {
  position: absolute;
  right: $icon-toggle-position;
  top: $icon-toggle-position;
}

.navigationLink {
  color: var(--main-color);
  font-size: 1.5em;
  line-height: $sidebar-line-heigth;
}

.navigationLink a {
  display: block;
}

.link {
  color: $color-button-text;
  font-weight: 100;
  display: block;
  font-size: 1.5em;
  line-height: 40px;
  text-decoration: none;
}
